'use client'

import { useEffect, useState } from 'react'
import { useAuthState } from 'react-firebase-hooks/auth'
import { auth, db } from '@/lib/firebase'
import {
  collection,
  doc,
  getDoc,
  getDocs,
  query,
  orderBy,
  Timestamp,
  addDoc,
  where,
} from 'firebase/firestore'
import { Loader2 } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Switch } from '@/components/ui/switch'
import { PADDLE_CONFIG } from '@/lib/constants'
import { identifyPlan } from '@/lib/utils'
import { PricePreview } from '@/components/dashboard/PricePreview'
import { toast } from 'sonner'

type Subscription = {
  subscriptionId: string
  customerId: string
  status: string
  planId: string
  priceId: string
  productId: string
  productName: string
  quantity: number
  amountPaid: number
  currency: string
  nextBillDate: string | null
  startDate: string
  billingCycle: {
    interval: string
    frequency: number
  }
  canceledAt?: string
  updatedAt: string
  createdAt: string
}

export default function Dashboard() {
  const [user] = useAuthState(auth)
  const [transactions, setTransactions] = useState<Subscription[]>([])
  const [billingCycle, setBillingCycle] = useState<'month' | 'year'>('month')
  const [loading, setLoading] = useState(true)
  const [subscription, setSubscription] = useState<Subscription | null>(null)

  useEffect(() => {
    if (!user) return

    fetchSubscription()
    fetchTransactions()
  }, [user])

  const fetchSubscription = async () => {
    try {
      const docRef = doc(db, 'users', user!.uid)
      const docSnap = await getDoc(docRef)

      const data = docSnap.data()
      if (!data?.currentSubscription) return

      const sub = data.currentSubscription
      setSubscription({
        ...sub,
        createdAt: sub.createdAt?.toDate().toISOString(),
        updatedAt: sub.updatedAt?.toDate().toISOString(),
      })
    } catch (error) {
      console.error('Error fetching subscription:', error)
    }
  }

  const fetchTransactions = async () => {
    try {
      const ref = collection(db, 'users', user!.uid, 'transactions')
      const q = query(ref, orderBy('createdAt', 'desc'))
      const snapshot = await getDocs(q)

      const txList = snapshot.docs.map((doc) => {
        const data = doc.data()
        return {
          ...data,
          createdAt: data.createdAt?.toDate().toISOString(),
          updatedAt: data.updatedAt?.toDate().toISOString(),
        } as Subscription
      })

      setTransactions(txList)
    } catch (error) {
      console.error('Error fetching transactions:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleCheckout = async (priceId: string) => {
    if (!user?.email) return

    // @ts-ignore
    const Paddle = window.Paddle

    Paddle?.Checkout.open({
      settings: {
        theme: 'light',
        displayMode: 'overlay',
      },
      items: [
        {
          priceId,
          quantity: 1,
        },
      ],
      customer: {
        email: user.email,
      },
      customData: {
        userId: user.uid,
      },
      onCheckoutComplete: async (event: any) => {
        try {
          const { data } = event
          const txRef = collection(db, 'users', user.uid, 'transactions')

          // Check if transaction already exists
          const existing = await getDocs(
            query(txRef, where('subscriptionId', '==', data.subscription.id))
          )
          if (!existing.empty) {
            toast.warning('Transaction already recorded.')
            return
          }

          await addDoc(txRef, {
            subscriptionId: data.subscription.id,
            customerId: data.customer.id,
            status: data.subscription.status,
            planId: data.items[0].price.id,
            priceId: data.items[0].price.id,
            productId: data.items[0].product.id,
            productName: data.items[0].product.name,
            quantity: data.items[0].quantity,
            amountPaid: data.items[0].amount,
            currency: data.items[0].price.currency_code,
            startDate: data.subscription.start_date,
            nextBillDate: data.subscription.next_billed_at,
            billingCycle: {
              interval: data.items[0].price.billing_cycle?.interval,
              frequency: data.items[0].price.billing_cycle?.frequency,
            },
            updatedAt: Timestamp.now(),
            createdAt: Timestamp.now(),
          })

          await fetchSubscription()
          await fetchTransactions()
        } catch (error) {
          console.error('Error saving transaction:', error)
        }
      },
    })
  }

  return (
    <div className="max-w-4xl mx-auto px-4 py-10">
      <div className="flex items-center justify-between mb-8">
        <h1 className="text-3xl font-bold text-slate-900">Dashboard</h1>
        <div className="flex items-center gap-2">
          <span className="text-sm text-slate-700">Yearly</span>
          <Switch
            checked={billingCycle === 'month'}
            onCheckedChange={(checked) =>
              setBillingCycle(checked ? 'month' : 'year')
            }
          />
          <span className="text-sm text-slate-700">Monthly</span>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-10">
        {PADDLE_CONFIG.products.all.map((product) => {
          const isCurrent =
            subscription?.planId === product[billingCycle].priceId

          return (
            <div
              key={product.id}
              className={`rounded-lg border p-4 shadow-sm ${
                isCurrent ? 'border-emerald-500' : ''
              }`}
            >
              <div className="text-lg font-semibold">{product.name}</div>
              <div className="text-sm text-gray-600">{product.description}</div>
              <PricePreview priceId={product[billingCycle].priceId} />
              <Button
                className="mt-4 w-full"
                disabled={isCurrent}
                onClick={() => handleCheckout(product[billingCycle].priceId)}
              >
                {isCurrent ? 'Current Plan' : 'Subscribe'}
              </Button>
            </div>
          )
        })}
      </div>

      <div>
        <h2 className="text-xl font-semibold mb-4">Transactions</h2>

        {loading ? (
          <div className="flex items-center gap-2">
            <Loader2 className="w-5 h-5 animate-spin" />
            <span>Loading transactions...</span>
          </div>
        ) : (
          <ul className="space-y-2">
            {transactions.map((tx, index) => (
              <li key={index} className="border rounded p-4">
                <div className="font-medium">{identifyPlan(tx.productName)}</div>
                <div className="text-sm text-gray-600">
                  {tx.currency} {tx.amountPaid}
                </div>
                <div className="text-sm text-gray-600">
                  Start: {tx.startDate}
                </div>
                <div className="text-sm text-gray-600">
                  Next Bill: {tx.nextBillDate || 'N/A'}
                </div>
                <div className="text-sm text-gray-600">
                  Status: {tx.status}
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  )
}
